html {
    &.loaded {
        .splash {
            display: none;
        }
        .head__logo-tagline {
            .logo, .tagline {
                opacity: 1;
                visibility: visible;
            }
        }
        .sub-head {
            opacity: 1;
            visibility: visible;
        }
    }
    &.ready {
        overflow-x: hidden;
        overflow-y: auto;
        height: auto;
        body {
            overflow-x: hidden;
            overflow-y: auto;
            height: auto;
        }
    }
    &.sign-up-state {
        overflow: hidden;
    }
}

body {
    &.dark {
        background-color: #333333;
    }
    &.log-in-state {

    }
    &.no-selection {
        user-select: none;
    }
}

.content-wrap {
    position: relative;
    max-width: 100%;
    padding-left: $page-padding-left;
    padding-right: $page-padding-right;
    box-sizing: border-box;
    margin: 0 auto;
    @include respond-to(desktop) {
        padding-left: $page-padding-left_desktop;
        padding-right: $page-padding-right_desktop;
    }
}

.splash {
    position: absolute;
    z-index: 10;
    width: 100%;
    height: 100%;
    background-color: white;
}

.preloader {
    $transition-time: 1s;

    position: relative;
    z-index: 2;
    width: 100%;
    height: 60%;
    transition: margin-top $transition-time ease-in-out, opacity $transition-time, visibility $transition-time;
    transform: translateZ(0);
    .canvas-wrap {
        position: relative;
    }
    canvas {
        position: relative;
        left: 5%;
        top: 10%;
        width: 90%;
        height: 80%;
    }
    &.hidden {
        opacity: 0;
        visibility: hidden;
    }
}

.enter-ico {
    position: absolute;
    right: 12px;
    top: 5px;
    width: 9px;
    height: 5px;
    border-right: 2px solid #AAAAAA;
    border-bottom: 2px solid #AAAAAA;
    @include respond-to(desktop) {
        right: 8px;
        top: 3px;
        width: 6px;
        height: 3px;
    }
    &:before {
        content: '';
        position: absolute;
        display: block;
        width: 1px;
        height: 1px;
        border: 5px solid transparent;
        border-right-color: #AAAAAA;
        left: -9px;
        bottom: -7px;
        @include respond-to(desktop) {
            border: 4px solid transparent;
            border-right-color: #AAAAAA;
            left: -9px;
            bottom: -6px;
        }
    }
}

.close-btn {
    position: absolute;
    right: $page-padding-right;
    top: 50%;
    transform: translateY(-50%);
    width: 20px;
    height: 20px;
    pointer-events: visiblePainted;
    @include respond-to(desktop) {
        right: $page-padding-right_desktop;
    }
    &:before, &:after {
        content: '';
        display: block;
        position: absolute;
        width: 0;
        height: 2px;
        background-color: #AAAAAA;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%) rotate(45deg);
        transition: all .35s;
        .log-in-state & {
            width: 24px;
            @include respond-to(desktop) {
                width: 16px;
            }
        }
    }
    &:after {
        transform: translate(-50%, -50%) rotate(-45deg);
    }
    .log-in-state & {
        cursor: pointer;
        &:hover {
            &:before, &:after {
                width: 21px;
                @include respond-to(desktop) {
                    width: 14px;
                }
            }
        }
    }
}

.main-btn-wrap {
    position: absolute;
    right: $page-padding-right;
    top: 50%;
    transform: translateY(-50%);
    padding-right: 33px;
    transition: all .5s;
    @include respond-to(desktop) {
        right: $page-padding-right_desktop;
        padding-right: 22px;
    }
    .enter-ico {
        visibility: hidden;
        opacity: 0;
        transition: all .35s;
    }
    .log-in-state & {
        right: $page-padding-right; // + 120px;
        @include respond-to(desktop) {
            right: $page-padding-right_desktop; // + 80px;
        }
        .enter-ico {
            visibility: visible;
            opacity: 1;
        }
    }
}

.main-btn {
    position: relative;
    @include f(m-semibold);
    font-size: 15px;
    letter-spacing: 10px;
    color: #333333;
    text-transform: uppercase;
    text-align: center;
    padding-left: 15px;
    padding-right: 6px;
    white-space: nowrap;
    cursor: pointer;
    transition: opacity 1s,
    visibility 1s;
    pointer-events: visiblePainted;
    @include respond-to(desktop) {
        font-size: 10px;
        letter-spacing: 4px;
    }
    .main-btn__hover-wrap {
        position: absolute;
        z-index: 2;
        left: 0;
        top: 0;
        width: 0;
        overflow: hidden;
        visibility: hidden;
        opacity: 0;
        transition: width .35s,
        visibility 0s linear .35s,
        opacity 0s linear .35s;
    }
    .main-btn__hover {
        background-color: #333333;
        color: white;
        padding-left: 15px;
        padding-right: 7px;
        box-sizing: border-box;
    }
    &:hover {
        .main-btn__hover-wrap {
            width: 100%;
            visibility: visible;
            opacity: 1;
            transition: width .35s,
            visibility 0s linear 0s,
            opacity 0s linear 0s
        }
    }
    &.hidden {
        opacity: 0;
        visibility: hidden;
    }
    .dark &, .log-in-state & {
        color: white;
        .main-btn__hover {
            background-color: white;
            color: #333333;
        }
    }
}

.input-field {
    position: relative;
    height: 75px;
    transition: all .35s;
    @include respond-to(desktop) {
        height: 50px;
    }
    input[type="text"], input[type="email"], input[type="password"] {
        display: block;
        position: relative;
        height: 40px;
        @include f(p-semibold);
        font-size: 21px;
        color: white;
        line-height: 40px;
        padding: 0;
        background-color: transparent;
        border: 0 !important;
        outline-width: 0;
        transform: translateY(5%);
        width: 100%;
        @include respond-to(desktop) {
            height: 27px;
            font-size: 14px;
            line-height: 27px;
        }
    }
    label {
        position: absolute;
        left: 0;
        top: 35%;
        @include f(m-semibold);
        font-size: 15px;
        letter-spacing: 6px;
        text-transform: uppercase;
        color: #AAAAAA;
        pointer-events: none;
        transform-origin: 0 0;
        transform: translateY(-50%);
        transition: all .35s;
        @include respond-to(desktop) {
            font-size: 10px;
            letter-spacing: 4px;
        }
        &.minified {
            top: -9px;
            transform: scale(.75) translateY(0);
            @include respond-to(desktop) {
                top: -6px;
            }
        }
    }
    .border-line {
        position: relative;
        height: 2px;
        background-color: #484848;
        transform: translateY(6px);
        &:before, &:after {
            content: '';
            display: block;
            position: absolute;
            width: 0;
            left: 0;
            bottom: 0;
            height: 2px;
            background-color: white;
            transition: all .35s;
        }
        &:after {
            width: 100%;
            visibility: hidden;
            opacity: 0;
            background-color: transparent;
            transition: all .35s;
        }
        &.active {
            &:before {
                width: 100%;
            }
            &:after {
                visibility: visible;
                opacity: 1;
            }
        }
    }
    &.strength_0 {
        .border-line:after {
            background-color: red;
        }
    }
    &.strength_1 {
        .border-line:after {
            background-color: orangered;
        }
    }
    &.strength_2 {
        .border-line:after {
            background-color: yellow;
        }
    }
    &.strength_3 {
        .border-line:after {
            background-color: greenyellow;
        }
    }
    &.strength_4 {
        .border-line:after {
            background-color: green;
        }
    }
}

.login-register {
    position: absolute;
    width: 500px;
    top: 50%;
    right: $page-padding-right;
    transform: translateY(-50%);
    transition: opacity .5s,
    visibility .5s;
    @include respond-to(desktop) {
        right: $page-padding-right_desktop;
        padding-right: 22px;
    }
    & > div {
        float: right;
        margin-left: 2px;
    }
    .line {
        width: 20px;
        height: 2px;
        background: #333333;
        position: relative;
        top: 50%;
        transform: translateY(-50%);
    }
    .main-btn-wrap {
        position: relative;
        right: 0;
        padding-right: 0;
        @include respond-to(desktop) {
            padding-right: 0;
        }
        .main-btn, .main-btn .main-btn__hover {
            padding-left: 15px;
            padding-right: 12px;
            @include respond-to(desktop) {
                padding-left: 10px;
                padding-right: 7px;
            }
        }
        .enter-ico {
            visibility: hidden;
            opacity: 0;
            transition: all .35s;
        }
        .log-in-state & {
            right: 120px;
            @include respond-to(desktop) {
                right: 80px;
            }
            .enter-ico {
                visibility: visible;
                opacity: 1;
            }
        }
    }
    &.hidden {
        opacity: 0;
        visibility: hidden;
    }
}

.mail-n-password {
    position: relative;
    width: 850px;
    top: 9px;
    @include respond-to(desktop) {
        width: 566px;
        top: 6px;
    }
    .input-field {
        width: 47%;
    }
    .input-field_email {
        float: left;
    }
    .input-field_password {
        float: right;
    }
}

.private-beta {
    position: relative;
    .private-beta__info {
        position: absolute;
        left: 0;
        top: 0;
        width: 400px;
        margin-right: 40px;
        @include respond-to(desktop) {
            width: 265px;
            margin-right: 25px;
        }
    }
    .private-beta__info__title {
        @include f(m-semibold);
        font-size: 15px;
        letter-spacing: 6px;
        line-height: 140%;
        color: white;
        text-transform: uppercase;
        width: 100px;
        margin-bottom: 20px;
        @include respond-to(desktop) {
            font-size: 10px;
            letter-spacing: 4px;
            width: 66px;
            margin-bottom: 14px;
        }
    }
    .private-beta__info__text {
        @include f(p-regular);
        font-size: 18px;
        line-height: 120%;
        color: #777;
        margin-bottom: 15px;
        width: 85%;
        @include respond-to(desktop) {
            font-size: 12px;
            margin-bottom: 10px;
        }
        a {
            color: white;
        }
    }
    .private-beta__sign-up {
        position: relative;
        margin-left: 440px;
        transform: translateY(-15px);
        @include respond-to(desktop) {
            margin-left: 290px;
            transform: translateY(-10px);
        }
        .input-field_name {
            margin-bottom: 15px;
            @include respond-to(desktop) {
                margin-bottom: 10px;
            }
        }
        .mail-n-password {
            left: 0;
            top: 0;
            width: 100%;
            margin-bottom: 60px;
            @include respond-to(desktop) {
                margin-bottom: 40px;
            }
        }
        .agree-submit {
            .agree {
                display: none;
                float: left;
                input[type="checkbox"] {
                    visibility: hidden;
                    display: none;
                    &:checked + label {
                        &:after {
                            visibility: visible;
                        }
                    }
                }
                label {
                    position: relative;
                    display: block;
                    @include f(p-regular);
                    font-size: 18px;
                    color: #777777;
                    white-space: nowrap;
                    padding-left: 40px;
                    cursor: pointer;
                    @include respond-to(desktop) {
                        font-size: 12px;
                        padding-left: 26px;
                    }
                    strong {
                        @include f(m-semibold);
                        text-transform: uppercase;
                        font-size: 15px;
                        color: #AAAAAA;
                        letter-spacing: 6px;
                        margin-left: 6px;
                        @include respond-to(desktop) {
                            letter-spacing: 4px;
                            margin-left: 4px;
                            font-size: 10px;
                        }
                    }
                    &:before {
                        content: '';
                        position: absolute;
                        display: block;
                        left: 0;
                        top: 0;
                        width: 20px;
                        height: 20px;
                        border-radius: 3px;
                        border: 1px solid #777777;
                        @include respond-to(desktop) {
                            width: 14px;
                            height: 14px;
                            border-radius: 2px;
                        }
                    }
                    &:after {
                        content: '';
                        position: absolute;
                        display: block;
                        width: 12px;
                        height: 6px;
                        border-left: 2px solid #AAAAAA;
                        border-bottom: 2px solid #AAAAAA;
                        left: 13px;
                        top: 3px;
                        transform: rotate(-45deg) translate(-50%, -50%);
                        visibility: hidden;
                        @include respond-to(desktop) {
                            width: 8px;
                            height: 4px;
                            left: 9px;
                            top: 2px;
                        }
                    }
                }
            }
            .submit {
                float: right;
                position: relative;
                padding-right: 33px;
                @include respond-to(desktop) {
                    padding-right: 22px;
                }
            }
        }
    }
}

.head {
    $showing-time: 3s;

    position: relative;
    z-index: 5;
    padding-top: 65px;
    margin-bottom: 60px;
    transition: opacity 5s;
    pointer-events: none;
    overflow-x: hidden;
    @include respond-to(desktop) {
        padding-top: 44px;
        margin-bottom: 40px;
    }
    .head__bar {
        position: absolute;
        top: 0;
        left: $page-padding-left;
        right: 0;
        height: 16px;
        @include respond-to(desktop) {
            left: $page-padding-left_desktop;
            height: 12px;
        }
        .bg {
            width: 100%;
            height: 100%;
            background-color: #333333;
            transform-origin: 0 0;
            transform: scaleX(0);
        }
        .progress {
            position: absolute;
            left: 0;
            top: 0;

            @include f(p-semibold);
            font-size: 24px;
            line-height: 75%;
            color: #333333;
            padding-left: 20px;
            transition: opacity .35s, visibility .35s;
            transform: translateZ(0);
            @include respond-to(desktop) {
                font-size: 18px;
                padding-left: 14px;
            }
            &.hidden {
                //opacity: 0;
                //visibility: hidden;
            }
        }
    }
}

.head__logo-tagline {
    $showing-time: 3s;
    .logo {
        //float: left;
        //width: 180px;

        @include f(m-semibold);
        font-size: 15px; //20px;
        line-height: 150%;
        letter-spacing: 6px;
        text-transform: uppercase;
        color: #333333;
        margin-bottom: 9px;

        opacity: 0;
        visibility: hidden;
        transition: opacity $showing-time,
        visibility $showing-time,
        color .5s;
        @include respond-to(desktop) {
            //width: 120px;
            margin-bottom: 6px;
            font-size: 10px; //14px;
            letter-spacing: 4px;
        }
        &.logo_big {
            display: block;
        }
        &.logo_small {
            display: none;
        }
        .dark &, .log-in-state & {
            color: white;
        }
    }
    .tagline {
        //float: left;
        //width: 165px;

        @include f(p-regular);
        font-size: 18px; //20px;
        line-height: 130%;
        color: #AAAAAA;

        opacity: 0;
        visibility: hidden;
        transition: opacity $showing-time, visibility $showing-time;
        transition-delay: $showing-time * .25;
        @include respond-to(desktop) {
            //width: 110px;
            font-size: 12px;
        }
        .log-in-state & {
            opacity: 0;
            visibility: hidden;
            transition: opacity .25s, visibility .25s;
            transition-delay: 0s;
        }
    }
    .mail-n-password {
        position: relative;
        float: left;
        width: 850px;
        @include respond-to(desktop) {
            width: 566px;
        }
    }
    .close-btn {
        right: 0;
        @include respond-to(desktop) {
            right: 0;
        }
    }
}

.head-fixed {
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
    width: 100%;
    height: 60px;
    background-color: #333333;
    transition: transform .35s;
    //box-shadow: 0 15px 45px rgba(0, 0, 0, .25);
    @include respond-to(desktop) {
        height: 40px;
        //box-shadow: 0 10px 30px rgba(0, 0, 0, .25);
    }
    .logo-tagline {
        .logo {
            float: left;
            width: 230px;

            @include f(m-semibold);
            font-size: 15px;
            letter-spacing: 7px;
            text-transform: uppercase;
            line-height: 60px;
            color: white;
            @include respond-to(desktop) {
                width: 150px;
                line-height: 40px;
                font-size: 10px;
                letter-spacing: 5px;
            }
            span {
                position: relative;
                display: inline-block;
                padding-right: 100px;
                @include respond-to(desktop) {
                    padding-right: 66px;
                }
                &:before {
                    content: '';
                    display: block;
                    position: absolute;
                    height: 2px;
                    width: 75px;
                    background-color: white;
                    top: 50%;
                    right: 0;
                    margin-right: 15px;
                    transform: translateY(-50%);
                    @include respond-to(desktop) {
                        margin-right: 10px;
                        width: 50px;
                    }
                }

            }
        }
        .tagline {
            float: left;
            width: auto;

            @include f(p-regular);
            font-size: 18px;
            color: #AAAAAA;
            line-height: 60px;
            white-space: nowrap;
            @include respond-to(desktop) {
                width: 70px;
                font-size: 12px;
                line-height: 40px;
            }
        }
    }
    .main-btn-wrap {
        top: 0;
        transform: translateY(0);
        @include respond-to(desktop) {
            transform: translateY(0);
        }
        .main-btn_login {
            color: white;
        }
        .main-btn__hover {
            background-color: white;
            color: #333333;
        }
    }
    .login-register {
        height: 18px;
        @include respond-to(desktop) {
            height: 12px;
        }
        .line {
            background-color: white;
        }
    }
    &.hidden {
        transform: translate3d(0, -100%, 0);
    }

    .touchevents & {
        display: none;
    }
}

.sub-head {
    $showing-time: 3s;

    position: relative;
    z-index: 5;

    @include f(p-regular);
    font-size: 21px;
    line-height: 120%;
    color: #333333;
    margin-bottom: 150px;

    opacity: 0;
    visibility: hidden;
    transition: opacity $showing-time ease-out $showing-time * .25,
    visibility $showing-time ease-out $showing-time * .25,
    color .5s;
    @include respond-to(desktop) {
        font-size: 14px;
        margin-bottom: 100px;
    }
    p {
        max-width: 1440px;
        @include respond-to(desktop) {
            max-width: 950px;
        }
    }
    body.dark &, .log-in-state & {
        color: white;
    }
}

