.main__slider {
    position: relative;
    margin-bottom: 100px;
    transition: opacity .5s,
    visibility .5s;
    @include respond-to(desktop) {
        margin-bottom: 66px;
    }
    .main__slider__i {
        float: left;
        position: relative;
        width: 50%;
        box-sizing: border-box;
        padding-left: $page-padding-left;
        @include respond-to(desktop) {
            padding-left: $page-padding-left_desktop;
        }
        .title {
            @include f(m-bold);
            font-size: 72px;
            line-height: 100%;
            text-transform: uppercase;
            letter-spacing: 15px;
            cursor: pointer;
            margin-bottom: 15px;
            pointer-events: visiblePainted;
            @include respond-to(desktop) {
                font-size: 48px;
                letter-spacing: 20px;
                margin-bottom: 10px;
            }
            p {
                visibility: hidden;
                color: gray;
            }
            svg {
                pointer-events: none;
                position: absolute;
                left: $page-padding-left;
                top: 0;
                @include respond-to(desktop) {
                    left: $page-padding-left_desktop;
                }
                text {
                    @include f(m-bold);
                    font-size: 72px;
                    line-height: 150%;
                    text-transform: uppercase;
                    letter-spacing: 15px;
                    color: white;
                    @include respond-to(desktop) {
                        font-size: 48px;
                        letter-spacing: 10px;
                    }
                }
            }
        }
        .sub-title {
            @include f(p-regular);
            font-size: 21px;
            line-height: 150%;
            color: #AAAAAA;
            width: 220px;
            margin-bottom: 45px;
            @include respond-to(desktop) {
                font-size: 14px;
                width: 145px;
                margin-bottom: 30px;
            }
        }
        .pagination {
            margin-bottom: 30px;
            @include respond-to(desktop) {
                margin-bottom: 20px;
            }
            .pagination__btn {
                position: relative;
                display: block;
                float: left;
                @include f(m-semibold);
                font-size: 18px;
                //color: #333333;
                margin-right: 90px;
                opacity: .3;
                pointer-events: visiblePainted;
                transition: opacity .35s, visibility .35s;
                @include respond-to(desktop) {
                    font-size: 12px;
                    margin-right: 60px;
                }
                &:before {
                    content: '';
                    display: block;
                    position: absolute;
                    left: 100%;
                    top: 50%;
                    width: 50px;
                    height: 2px;
                    background-color: #333333;
                    visibility: hidden;
                    transform: translate(18px, -50%);
                    pointer-events: none;
                    @include respond-to(desktop) {
                        width: 35px;
                        height: 2px;
                        transform: translate(12px, -50%);
                    }
                }
                &.pagination__btn_visited {
                    opacity: 1;
                    cursor: pointer;
                    &:before {
                        visibility: visible;
                    }
                }
                &.pagination__btn_active {
                    opacity: 1;
                }
                &.hidden {
                    opacity: 0;
                    visibility: hidden;
                }
            }
        }
        .hint {
            position: relative;
            width: 375px;
            transition: opacity 1s, visibility 1s;
            @include respond-to(desktop) {
                width: 250px;
            }
            .hint-title {
                position: relative;

                @include f(m-bold);
                font-size: 18px;
                color: #333333;
                text-transform: uppercase;
                letter-spacing: 5px;
                line-height: 120%;
                margin-bottom: 8px;
                @include respond-to(desktop) {
                    margin-bottom: 5px;
                    font-size: 12px;
                    letter-spacing: 3px;
                }
                .slide-ico {
                    position: relative;
                    display: inline-block;
                    width: 24px;
                    height: 24px;
                    border-radius: 50%;
                    border: 1px solid #AAAAAA;
                    margin-left: 15px;
                    transform: translateY(6px);
                    @include respond-to(desktop) {
                        width: 16px;
                        height: 16px;
                        margin-left: 10px;
                        transform: translateY(4px);
                    }
                    &:before, &:after {
                        position: absolute;
                        display: block;
                        content: '';
                        width: 3px;
                        height: 3px;
                        border-top: 1px solid #AAAAAA;
                        border-right: 1px solid #AAAAAA;
                        left: 50%;
                        top: 50%;
                        @include respond-to(desktop) {
                            width: 2px;
                            height: 2px;
                        }
                    }
                    &:before {
                        transform: translate(-50%, -110%) rotate(-45deg);
                    }
                    &:after {
                        transform: translate(-50%, 10%) rotate(135deg);
                    }
                }
            }
            .hint-text {
                @include f(p-regular);
                font-size: 21px;
                color: #AAAAAA;
                line-height: 120%;
                @include respond-to(desktop) {
                    font-size: 14px;
                }
            }
            .hint-arrow {
                position: absolute;
                left: 100%;
                top: 50%;
                width: 100%;
                height: 100%;
                transition: opacity .35s,
                visibility .35s;
                svg {
                    width: 100%;
                    height: 100%;
                }
                &.hidden {
                    opacity: 0;
                    visibility: hidden;
                }
            }
            &.hidden {
                visibility: hidden;
                opacity: 0;
                transition: opacity .35s, visibility .35s;
            }
        }
    }
    &.hidden {
        opacity: 0;
        visibility: hidden;
    }
}

