    }
}

.no-transition {
    transition: none !important;
}

.mobile {
    display: none;

    position: relative;
    z-index: 9000;
    width: 100%;
    height: 100%;
    background-color: white;
    padding-left: 46px;
    overflow-y: scroll;
    @include respond-to(mobile) {
        padding-left: 24px;
    }
    .head-bar {
        height: 15px;
        background-color: #333333;
        margin-bottom: 25px;
        @include respond-to(mobile) {
            height: 10px;
            margin-bottom: 17px;
        }
    }
    .logo {
        @include f(m-semibold);
        font-size: 15px;
        letter-spacing: 4px;
        text-transform: uppercase;
        line-height: 200%;
        color: #333333;
        @include respond-to(mobile) {
            font-size: 10px;
            letter-spacing: 2px;
        }
    }
    .tagline {
        @include f(p-regular);
        font-size: 18px;
        color: #777777;
        line-height: 120%;
        white-space: nowrap;
        margin-bottom: 115px;
        @include respond-to(mobile) {
            font-size: 12px;
            margin-bottom: 60px;
        }
    }
    .num {
        @include f(m-semibold);
        font-size: 18px;
        color: #333333;
        margin-bottom: 30px;
        @include respond-to(mobile) {
            font-size: 12px;
            margin-bottom: 18px;
        }
    }
    .title {
        @include f(m-bold);
        font-size: 36px;
        text-transform: uppercase;
        letter-spacing: 10px;
        color: #333333;
        margin-bottom: 20px;
        line-height: 100%;
        @include respond-to(mobile) {
            font-size: 20px;
            letter-spacing: 6px;
            margin-bottom: 12px;
        }
    }
    .sub-title {
        @include f(m-regular);
        font-size: 24px;
        line-height: 150%;
        color: #AAAAAA;
        margin-bottom: 80px;
        @include respond-to(mobile) {
            font-size: 12px;
            margin-bottom: 50px;
        }
    }
    .full-experience {
        position: relative;
        display: inline-block;
        padding: 20px 36px;
        border: 2px solid #eb212e;
        margin-top: 35px;
        margin-bottom: 36px;
        @include respond-to(mobile) {
            padding: 10px 18px;
            border: 1px solid #eb212e;
            margin-top: 18px;
            margin-bottom: 20px;
        }
        p {
            @include f(m-bold);
            font-size: 18px;
            text-transform: uppercase;
            letter-spacing: 4px;
            color: #333333;
            line-height: 120%;
            white-space: nowrap;
            @include respond-to(mobile) {
                font-size: 10px;
                letter-spacing: 2px;
            }
        }
    }

    .touchevents & {
        display: block;
    }
}
